* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
* ::after,
* ::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ol,
ul {
  list-style: none;
}
body {
  overflow: hidden;
}
#page {
  display: flex;
  flex-wrap: wrap;
}
#page > section {
  width: 50vw;
  height: 50vh;
  border: 1px solid grey;
}
